import React from 'react';
import '../assets/style/Footer.scss'
import {useLocation} from "react-router-dom";

const Footer = () => {
        let footer;
        if (useLocation().pathname === "/"){
            footer=document.getElementById("footer");
            if (footer != null) {
                footer.style["marginTop"] = "2500px";
            }
        }
        else {
            footer=document.getElementById("footer");
            if (footer != null)
                footer.style["marginTop"] = "100px";
        }
        return (
            <div id="footer">
                <div className="fInner" >
                    <div className="ttl"><span></span> </div>
                    <div className="inner clearfix">
                        <div className="img"><img src={[require("../assets/img/f_img.png")]} width="136" alt=""/></div>
                        <div className="rBox">
                            <p className="txt">1517881330@qq.com &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
                                &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
                                QQ1517881330 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
                                &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;21120
                            </p>
                            <p className="copy">
                                2020  
                                <a href="https://beian.miit.gov.cn/" target="_blank">ICP20030061</a>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }


export default Footer;
