import React from 'react';
import Slider from 'react-slick'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import '../../assets/style/IndexPage.scss'
import 'antd/dist/antd.css'
import img1 from "../../assets/img/image-Example-1.png"
import img2 from "../../assets/img/image-Example-2.png"
import teacher1 from "../../assets/img/teacher1.png"
import teacher2 from "../../assets/img/teacher2.png"
import abilityRaise from "../../assets/img/abilityRaise.png"
import caowenpic from "../../assets/img/caowen.jpg"
import jinbinpic from "../../assets/img/jinbin.jpg"
import chenjunpic from "../../assets/img/chenjun.jpg"
import jiangzhongtianpic from "../../assets/img/jiangzhongtian.jpg"
import {Popover, Space, Typography} from "antd"
import './index.scss'
import $ from 'jquery'
import Footer from "../Footer";

const Index = () => {
    $(function(){
        $('.main .section05 .movie a,.main .section05 .btn a').click(function(){
            $('.cover,.pop').fadeIn();
            console.log("")
            return false;
        });
        $('.pop .close a').click(function(){
            $('.cover,.pop').fadeOut();
            $('.pop video').trigger('pause');
            return false;
        });
    });
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
        adaptiveHeight: true,
    };

    const contentStyle: React.CSSProperties = {
        height: '160px',
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        background: '#364d79',
    };
    const { Title, Paragraph, Text, Link } = Typography;

    const caowen = (
        <section className="specialAdvisor">
            <Title> / </Title><p></p>
            <blockquote>NOI</blockquote>
            <p className="txt">198930<br/>
                 Topcoder Open1<br/>
                 IOI411<br/>
                 (NOI)<br/>
                 NOIP<br/>
                 60<br/>
                 985<br/>
                 20205<br/>
                 2021()<br/>
                 Hopcroft
            </p>
        </section>
    );
    const jinbin = (
        <section className="specialAdvisor">
            <Title> / </Title>
            <blockquote>NOI2008TCOTCHSACM</blockquote>
            <p className="txt">
                NOI2008NOIP200720062009TCOTCHS
                TopCoderMike Lydon2010
                ACMACM2010ACM
                
            </p>
        </section>
    )
    const chenjun = (
        <section className="specialAdvisor">
            <Title> / </Title>
            <blockquote>PV</blockquote>
            <p className="txt">
                19933199497
                LinuxLinuxLinuxLinux
                LinuxLinux
                LinuxTurboLinux2006-2014PV
                2014
                
            </p>
        </section>
    )
    const jiangzhongtian = (
        <section className="specialAdvisor">
            <Title> / </Title>
            <blockquote>NOI2009IOI2011</blockquote>
            <p className="txt">
                NOI2009NOI2009 2
                201172823IOI2011 20
                118 2012ACM
                 36ACM/ICPC 
                Pallet PackingFacebookGoogle500 
                Uber 
            </p>
        </section>
    )
    let imgArray = [img1,img2,img1,img2,img1,img2];

    return (
        <Typography className="main">
            <section>
                <div style={{width:"1800px"}}>
                    <Slider {...settings}>
                        {
                            imgArray.map((item, id)=>
                                <div className={"index-img-wrapper"}>
                                    <img src={imgArray[id]} alt=""/>
                                </div>
                            )
                        }
                    </Slider>
                </div>
                <br/>
                <br/>
                <br/>
                <br/>
            </section>
            <section>
                <Title></Title>
                <blockquote>C++</blockquote>
                <img src={abilityRaise}/>
                <br/>
                <br/>
                <br/>
                <br/>
            </section>
            <section>
                <Title></Title>
                <blockquote></blockquote>
                <section>
                    <div style={{width:"1800px"}}>
                        <Slider {...settings}>
                            <div className={"index-img-wrapper"}>
                                <img src={teacher1} style={{width:"1400px", height:"500px"}} alt=""/>
                            </div>
                            <div className={"index-img-wrapper"}>
                                <img src={teacher2} style={{width:"1400px", height:"500px"}} alt=""/>
                            </div>
                        </Slider>
                    </div>
                </section>
                <br/>
                <br/>
                <br/>
                <br/>
            </section>
            <section className="section03">
                <section>
                    <Title></Title>
                    <blockquote></blockquote>
                </section>
                <Space wrap>
                    <Popover content={caowen}  trigger="hover">
                        <ul className="tabNavi">
                            <li key={1}><a><span><img src={caowenpic} alt=""/></span></a></li>
                        </ul>
                    </Popover>
                    <Popover content={jinbin}  trigger="hover">
                        <ul className="tabNavi">
                            <li key={2}><a><span><img src={jinbinpic} alt=""/></span></a></li>
                        </ul>
                    </Popover>
                    <Popover content={chenjun}  trigger="hover">
                        <ul className="tabNavi">
                            <li key={3}><a><span><img src={chenjunpic} alt=""/></span></a></li>
                        </ul>
                    </Popover>
                    <Popover content={jiangzhongtian}  trigger="hover">
                        <ul className="tabNavi">
                            <li key={4}><a><span><img src={jiangzhongtianpic} alt=""/></span></a></li>
                        </ul>
                    </Popover>
                </Space>
            </section>

            <section className="section04">
                <div className="wrap">
                    <div className="inner clearfix">
                        <div className="photoBox">
                            <img className="img01" src={[require("../../assets/img/img16.png")]} alt=""/>
                            <img className="img02" src={[require("../../assets/img/img17.png")]} alt=""/>
                            <img className="img03" src={[require("../../assets/img/img18.png")]} alt=""/>
                            <img className="img04" src={[require("../../assets/img/img19.png")]} alt=""/>
                            <img className="img05" src={[require("../../assets/img/img20.png")]} alt=""/>
                            <img className="img06" src={[require("../../assets/img/img21.png")]} alt=""/>
                        </div>
                        <div className="txtBox">
                            <p className="ttl"><span></span>Why did you choose us ?</p>
                            <p className="txt"></p>
                            <ul className="clearfix">
                                <li className="li01" key={1}><span>20+</span></li>
                                <li key={2}><span>1000+</span></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            <section className="section05">
                <div className="wrap">
                    <div className="inner clearfix">
                        <div className="movie"><a href="#"/></div>
                        <div className="txtBox">
                            <p className="ttl"><span></span> </p>
                            <p className="txt">AI?</p>
                            <p className="txt">C++<br/>
                                <br/>
                                </p>
                            <div className="btn"><a href="#"></a></div>
                        </div>
                    </div>
                </div>
            </section>
            <div>
                <div className="cover"/>
                <div className="pop">
                    <div className="close"><a href="#"/></div>
                    <div className="video">
                        <video src="https://vedio.code101.com.cn/58fc0d5ca9a14d0b91905a37ddeab8eb/858c3ac9f3abb7d7c1bd47f6271c4383-hd.mp4" controls/>
                    </div>
                </div>
            </div>
            <Footer/>
        </Typography>
    );
}

export default Index;
